import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './scss/best.scss'
import './scss/collection.scss'
import './scss/banner.scss'
import Slider from './component/Slider';
import { useNavigate } from 'react-router-dom'

const Home = (props) => {
    const { best } = props
    const navigate = useNavigate();
    return (
        <>
            <Slider />
            <div style={{ paddingBottom: "10vh" }}></div>
            <Container className="bestbox">
                <h2>BEST</h2>
                <Row className="g-4">
                    {
                        best.slice(0, 4).map((best, i) =>
                            <Col sm={6} xl={3} key={i} onClick={()=>{
                                navigate('/bestproduct/'+best.urlname)
                            }}>
                                <img src={process.env.PUBLIC_URL + best.imgUrl} alt="bset"></img>
                                <h4>{best.title}</h4>
                                <div className="priceall">
                                    <h5>{best.price.toLocaleString()}</h5>
                                    <h5>{best.saleprice.toLocaleString()}</h5>
                                    <h6>{best.sale}</h6>
                                </div>
                            </Col>
                        )
                    }
                </Row>
                <h6 className='more' onClick={()=>{navigate('/best')}}>BEST  </h6>
            </Container>

            <div className="bannerbox">
                <div className='imgbox'><img src={process.env.PUBLIC_URL + "/img/banner1.jpg"} alt="banner" /></div>
                <div className='imgbox'><img src={process.env.PUBLIC_URL + "/img/banner2.jpg"} alt="banner" /></div>

            </div>

            <Container className='collectionbox'>
                <h2>COLLECTION</h2>
                <Row className="g-4">
                    <Col md={4}>
                        <div className='collection1'><img src={process.env.PUBLIC_URL + "/img/col1.jpg"} alt="collection" />
                        </div>
                    </Col>
                    <Col md={4}>
                        <div className='collection2'><img src={process.env.PUBLIC_URL + "/img/col2.jpg"} alt="collection" />
                        </div>
                    </Col>
                    <Col md={4}>
                        <div className='collection3'><img src={process.env.PUBLIC_URL + "/img/col3.jpg"}alt="collection" />
                        </div>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default Home