import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './scss/best.scss'
import { useNavigate } from 'react-router-dom';

const Best = (props) => {
  const { best } = props;
  const navigate = useNavigate();

  return (
    <>
      <Container className="bestbox mt-5">
        <h3>FANCY CLUB - BEST</h3>
        <Row className="g-4">
          {
            best.map((best, i) =>
              <Col sm={6} xl={3} key={i} onClick={()=>{
                navigate('/bestproduct/'+best.urlname)
              }}>
                <img src={process.env.PUBLIC_URL + best.imgUrl} alt="bset"></img>
                <h4>{best.title}</h4>
                <div className="priceall">
                  <h5>{best.price.toLocaleString()}</h5>
                  <h5>{best.saleprice.toLocaleString()}</h5>
                  <h6>{best.sale}</h6>
                </div>
              </Col>
            )
          }
        </Row>
      </Container>
      <Row>
        <Col></Col>
        <Col></Col>
        <Col></Col>
        <Col></Col>
      </Row>
    </>
  )
}

export default Best