import { Container, Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import '../scss/cart.scss';
import { removeItem, increaseCount, decreaseCount } from '../cart';

const Cart = () => {
    let cart = useSelector(state => state.cart);
    const dispatch = useDispatch();
    const totalPrice = cart.reduce((acc, item) => acc + item.price * item.count, 0);
    const totalSalePrice = cart.reduce((acc, item) => acc + item.saleprice * item.count, 0);
    const discount = totalPrice - totalSalePrice;
    const shipping = totalSalePrice > 50000 ? 0 : 3000;
    const finalTotal = totalSalePrice + shipping;

    return (
        <>
            <Container>
                <Table className='mt-4'>
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody className='cartit'>
                        {
                            cart.map((best, i) =>
                                <tr key={i}>
                                    <td><img src={process.env.PUBLIC_URL + best.front} alt="bi" width="100px" /></td>
                                    <td>{best.title}</td>
                                    <td>
                                        <div className="count">
                                            <button onClick={() => {
                                                if (best.count < 2) {
                                                    alert("1    .");
                                                } else { dispatch(decreaseCount(i)); }
                                            }} ><img src={process.env.PUBLIC_URL + "/img/minus.png"} alt="m" /></button>
                                            <input type="text" value={best.count} readOnly name="count" className="price_value" />
                                            <button onClick={() => dispatch(increaseCount(i))}><img src={process.env.PUBLIC_URL + "/img/plus.png"} alt="p" /></button>
                                        </div></td>
                                    <td><h4>{best.price.toLocaleString()}</h4> <h4>{best.saleprice.toLocaleString()}</h4>
                                    </td>
                                    <td><button onClick={() => dispatch(removeItem(i))}></button></td>
                                </tr>
                            )
                        }
                    </tbody>
                </Table>
                <Table className='mt-5'>
                    <thead>
                        <tr>
                            <th> </th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody className='pricebox'>
                        <tr>
                            <td><h4>{totalPrice.toLocaleString()}</h4></td>
                            <td><h4>{shipping.toLocaleString()}</h4></td>
                            <td><h4>-{discount.toLocaleString()}</h4></td>
                            <td><h4>{finalTotal.toLocaleString()}</h4></td>
                        </tr>
                    </tbody>
                </Table>
            </Container >
        </>
    )
}

export default Cart