import React from 'react'
import { Carousel } from 'react-bootstrap'
import '../scss/slider.scss';


const Slider = () => {
    return (
        <Carousel>
            <Carousel.Item interval={6000}>
                <img className="d-block w-100" src={process.env.PUBLIC_URL + "/img/main_slider2.jpg"} alt="slider" />
            </Carousel.Item>
            <Carousel.Item interval={6000}>
                <img className="d-block w-100" src={process.env.PUBLIC_URL + "/img/main_slider.jpg"} alt="slider" />
            </Carousel.Item>
        </Carousel>
    )
}

export default Slider