import React from 'react'
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import '../scss/nav.scss';
import { useNavigate } from 'react-router-dom';

const NavComp = () => {
    const navigate = useNavigate();
    return (
        <Navbar collapseOnSelect expand="lg" className="bg-body-tertiary">
            <Container>
                <Navbar.Brand><img className="logo" src={process.env.PUBLIC_URL + "/img/logo.png"} alt="logo" onClick={()=>{
                    navigate("/")
                }} /></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link onClick={()=>{navigate('/best')}}>BEST</Nav.Link>
                        <Nav.Link href="#">COLLECTION</Nav.Link>
                        <NavDropdown title="SHOP" id="collapsible-nav-dropdown">
                            <NavDropdown.Item href="#">2025 S/S</NavDropdown.Item>
                            <NavDropdown.Item href="#">
                                ALL
                            </NavDropdown.Item>
                            <NavDropdown.Item href="#">OUTER</NavDropdown.Item>
                            <NavDropdown.Item href="#">TOP</NavDropdown.Item>
                            <NavDropdown.Item href="#">BOTTOM</NavDropdown.Item>
                            <NavDropdown.Item href="#">DRESS</NavDropdown.Item>
                            <NavDropdown.Item href="#">BOTTOM</NavDropdown.Item>
                            <NavDropdown.Item href="#">ACC</NavDropdown.Item>
                            <NavDropdown.Item href="#">SETUP</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                    <Nav>
                        <Nav.Link href="#">LOGIN</Nav.Link>
                        <Nav.Link onClick={()=>{navigate('/cart')}}>CART</Nav.Link>
                        <Nav.Link href="#">SEARCH</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default NavComp