import { createSlice } from '@reduxjs/toolkit'

const cart = createSlice({
    name: 'cart',
    initialState: [],
    reducers: {
        addCount(state, action) {
            state.push(action.payload)
        },
        sortName(state) {
            state.sort((a, b) => a.item > b.item ? 1 : -1)
        },
        addItem(state, action) {
            state.push(action.payload)
        },
        sortPrice(state) {
            state.sort((a, b) => a.item > b.item ? 1 : -1)
        },
        removeItem(state, action) {
            const i = action.payload;
            state.splice(i, 1);
        },
        increaseCount(state, action) {
            const item = state[action.payload];
            if (item) item.count = Number(item.count) + 1;
        },
        decreaseCount(state, action) {
            const item = state[action.payload];
            if (item && item.count > 1) item.count -= 1;
        }
    }
})

export let { addCount, sortName, addItem, sortPrice, removeItem, increaseCount, decreaseCount } = cart.actions;

export default cart.reducer;